@charset "utf-8";

// Import modern SASS modules for namespaced functions
@use "sass:math";
@use "sass:color";
@use "sass:list";

// Import modern utilities (replaces Bourbon 4.2.2 & Neat 1.7.2)
// Note: Using @import here because the base/ partials use @import internally
// Converting to @use would require restructuring all base files (future work)
// These @import warnings are expected and low priority (Dart Sass 3.0 not yet scheduled)
@import "utilities/utilities";

// Import partials from `sass_dir` (defaults to `_sass`)
// Includes Bitters base styles and custom components
@import
  "base/base",
  "lightgallery/lightgallery",
  "layout",
  "gallery",
  "modal",
  "default.css"
;
