  width:100%;
  overflow-x: auto
}

iframe {
  max-width: 100%;
  margin: auto;
  display: block;
}

.gallery-3-col {
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  img {
    display: inline;
    width: 30%
  }

}

.gallery-2-col {
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  img {
    display: inline;
    width: 47%
  }

}

.large-table {
    max-width: 100%;
